@import '../Styles/include-media';
@import '../Styles/mediaqueries';

.leaflet-container {
  height: 100vh;
  width: 100%;
  position: sticky !important;
  top: 0px;
  left: 0px;
}

.leaflet-control-container {
  display: none;
}

.intermap {
  padding: 0;
  height: 250vh;
  &__desc {
    margin-top: -100vh;
    width: 100%;
    position: sticky;
    right: 0;
    top: 0;
    z-index: 500;
    font-size: 100px;
    padding-right: 70px;
    box-sizing: border-box;
    text-align: right;
    margin-bottom: 40vh;
    span {
      font-size: 40px;
      font-weight: 700;
      letter-spacing: 0.1em;
    }
    @include media($media-mobile...) {
      padding-right: 25px;
      span {
        font-size: 20px;
      }
    }
  }
  &__events {
    z-index: 2;
    position: sticky;
    text-align: left;
    margin-top: 50vh;
    top: calc(100vh - 120px);
    z-index: 500;
    transform: translateY(-100%);
    font-size: 20px;
    line-height: 2;
    width: calc(40% - 56px);
    letter-spacing: 0.1em;
    margin-left: calc(60% - 14px);
    position: sticky;
    right: 0;
    z-index: 500;
    margin-right: 70px;
    box-sizing: border-box;
    span {
      padding: 0 0.3em;
    }
    @include media($media-mobile...) {
      font-size: 16px;
      margin-left: calc(20% + 15px);
      width: calc(80% - 40px)
    }
  }
  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;;
    top: 0;
  }
}